.ghost-white-button {
  border: 2px solid #ffffff;
  border-radius: 0px;
  background-color: transparent;
  color: #ffffff;
  transition: all .35s;
  -webkit-transition: all .35s;
  -moz-transition: all .35s;
}
.ghost-white-button:hover {
  background-color: #ffffff;
  color: #0095ff;
  border-color: #ffffff;
}
